import React, { useState, useEffect } from 'react';
import dayjs from 'dayjs';
import timezone from "dayjs/plugin/timezone";
import utc from "dayjs/plugin/utc";
const ambient = require('ambient-lib')
dayjs.extend(timezone);
dayjs.extend(utc);

export interface sensorData {
    time: String;
    co2: number;
    temperature: number;
    humidity:number;
  }
  
export function Ambient() {

    // const [co2, setCo2] = useState(0);
    // const [temperature, setTemperature] = useState(0);
    // const [humidity, setHumidity] = useState(0);
    const [sensor , setSensorData]  = useState<sensorData>({time:"",co2:0,temperature:0,humidity:0})
    const intervalMs = 5000;

    const readambient = () =>{
        let am  = new ambient(47640	, "4d763fd967e09056", "162cd70772e23752");
        am.read({n: 1}, function(response:any) {
            // console.log(response.data);
            // setCo2(response.data[0].d1);
            // setTemperature(response.data[0].d2);
            // setHumidity(response.data[0].d3);
            let created = dayjs(new Date(response.data[0].created)).tz("Asia/Tokyo").format(`HH:mm:ss`);
            let co2 = response.data[0].d1;
            let temperature = response.data[0].d2;
            let humidity = response.data[0].d3;
            setSensorData({time:created,co2:co2,temperature:temperature,humidity:humidity});
        }, 10000);
 
    }

    const readambient_all = () =>{
        let am  = new ambient(47640	, "4d763fd967e09056", "162cd70772e23752");
        am.read({n: 1000}, function(response:any) {
            console.log(response.data);
            // setCo2(response.data[0].d1);
            // setTemperature(response.data[0].d2);
            // setHumidity(response.data[0].d3);
            let created = dayjs(new Date(response.data[0].created)).tz("Asia/Tokyo").format(`HH:mm:ss`);
            let co2 = response.data[0].d1;
            let temperature = response.data[0].d2;
            let humidity = response.data[0].d3;
            setSensorData({time:created,co2:co2,temperature:temperature,humidity:humidity});
        }, 60000);
 
    }

    useEffect(() => {
        // Update the document title using the browser API
        const intervalId = setInterval(() => {
            // 定期実行する関数
            readambient();
       }, intervalMs);
       const intervalId_all = setInterval(() => {
        // 定期実行する関数
        readambient_all();
   }, intervalMs);
    return () => {
        clearInterval(intervalId);
        clearInterval(intervalId_all);
    };

    


      });


    return (
        <div>
            {sensor.time}<br/>
            {sensor.co2}<br/>
            {sensor.humidity}<br/>
            {sensor.temperature}
        </div>
    )
    }